.flip-card {
  background-color: transparent;
  width: 50px;
  height: 50px;
  perspective: 1000px;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.flip-card.flip .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  cursor: default;
}

.flip-card-front {
  background-color: #2980b9;
  color: black;
}

.flip-card-back {
  background-color: rgb(53, 182, 124);
  color: white;
  transform: rotateY(180deg);
  text-align: center;
  font-size: 30px;
  line-height: 50px;
  font-weight: bold;
  box-shadow: inset 0 0 0 2px #fff;
}

.board {
  width: 206px;
  height: 206px;
  flex-wrap: wrap;
  display: flex;
  justify-content: space-between;
  border: solid 1px #ccc;
  position: relative;
  overflow: hidden;
}

.hidden {
  opacity: 0;
  transition: all 0.3s;
}

.not-start:after {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.5);
  content: "";
  z-index: 1;
}

.btn-start {
  z-index: 2;
  font-size: 20px;
  line-height: 30px;
  width: 150px;
  text-align: center;
  height: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  cursor: pointer;
  background: red;
  color: #fff;
  border-radius: 30px;
}

.div-amount span {
  text-align: right;
  display: inline-block;
  width: 160px;
  padding-right: 20px;
}

.div-amount input {
  width: 50px;
}

body {
  font-family: sans-serif;
  margin: 0;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: #efefef;
}

.nav {
  ul {
    padding: 0;
    margin: 0 0 30px 0;
    overflow: hidden;
    background: #fff;
  }

  li {
    list-style: none;
    display: inline-block;
    padding: 0 10px;
    position: relative;
    margin: 3px;
  }

  li+li {
    border-left: solid 2px #e1d5bf;
  }

  a {
    line-height: 30px;
    font-weight: bold;
    color: #676c7f;
    text-decoration: none;

    &.active {
      color: #ca8799;
    }
  }

}

.lb-home {
  width: 50%;
  position: absolute;
  top: 30%;
  left: 50%;
  height: 100px;
  transform: skew(-30deg) translateX(-50%);
}

.txt-home {
  color: #ca8799;
  text-align: center;
  font-size: 36px;
  background: #fff;
  border: solid 2px #e1d5bf;
  z-index: 2;
  line-height: 80px;
  position: absolute;
  top: 0;
  width: 100%;
  text-shadow:1px 1px 10px mix(#fff, #ca8799, 40%);

  &:after,
  &:before {
    content: "";
    width: 3px;
    height: 20px;
    position: absolute;
    background: #e1d5bf;
  }

  &:after {
    bottom: -20px;
    right: 7px;
    transform: skew(-45deg);
  }

  &:before {
    top: -20px;
    left: 7px;
    transform: skew(-45deg);
  }

}

.lb {
  background: #fff;
  border: solid 2px #e1d5bf;
  height: 80px;
  width: 80px;
  position: absolute;
  z-index: 1;

  &:after,
  &:before {
    content: "";
    width: 20px;
    height: 40px;
    position: absolute;
    background: #efefef;
  }

  &.l {
    left: -60px;
    top: -20px;

    &:after {
      transform: skew(22deg);
      left: -13px;
      border-right: solid 2px #e1d5bf;
    }

    &:before {
      transform: skew(-20deg);
      left: -13px;
      bottom: 0;
      border-right: solid 2px #e1d5bf;
    }
  }

  &.r {
    right: -65px;
    top: 20px;
    &:after {
      transform: skew(-20deg);
      right: -14px;
      border-left: solid 2px #e1d5bf;
    }

    &:before {
      transform: skew(20deg);
      right: -14px;
      bottom: 0;
      border-left: solid 2px #e1d5bf;
    }
  }
}

.div-time {
  margin-top: 20px;
}

.div-clear {
  position: absolute;
  top: 50%;
  right: 0;
  left: 0;
  font-size: 40px;
  color: red;
  text-shadow: 3px 3px 3px #999;
  text-align: center;
  transform: translateY(-50%);
  animation-name: clear;
  animation-duration: 500ms;
}

@keyframes clear {
  from {
    top: 120%;
    font-size: 10px;
  }

  to {
    top: 50%;
    font-size: 40px;
  }
}

.hide {
  display: none;
}

.btn {
  font-size: 14px;
  line-height: 24px;
  border-radius: 8px;
  background: #efefef;
  border: solid 1px #afafaf;
  cursor: pointer;

  &:hover {
    color: #ca8799;
  }
}

.container {
  margin: auto;
  padding: 10px;
}

@media (max-width: 780px) {
