$aColor: #e9e05f;
$bColor: #1E4FFF;
$cColor: #E203AE;
@mixin itemColor($color) {
  border: solid 1px darken($color, 30%);
  background: $color;
}
.item {
  width: 30px;
  height: 30px;
  font-weight: bold;
  text-align: center;
  line-height: 30px;
  box-sizing: border-box;
  &.a {
    @include itemColor($aColor);
  }
  &.b {
    @include itemColor($bColor);
  }
  &.c {
    @include itemColor($cColor);
  }
}

.item.focus {
  color: red;
  &.a {
    @include itemColor(mix(#fff, $aColor, 35%));
  }
  &.b {
    @include itemColor(mix(#fff, $bColor, 35%));
  }
  &.c {
    @include itemColor(mix(#fff, $cColor, 35%));
  }
}

@media (min-width: 781px) {
  .container {
    width: 80%;
  }
  .box {
    width: 45%;
